<template>
    <div class="goSearch">
        <van-search
          v-model="value"
          show-action
          placeholder="美妆爆款满199减100"
        >
          <template #action>
            <div name="apps-o" @click="goList(value)">搜索</div>
          </template>
          <template #left>
            <van-icon name="arrow-left" @click="back" size="25" color="rgb(102,102,102)"/>
          </template>
        </van-search>
        <div class="content">
            <p>热门搜索</p>
            <ul>
                <li v-for="(item,index) in contentData" :key="index" @click="goList(item.value)">{{item.value}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
    import "../assets/css/goSearch.less"
    export default {
        data(){
            return{
                value:'',
                contentData:[
                    {
                        value:'炒锅',
                    },
                    {
                        value:'床垫',
                    },
                    {
                        value:'洗发水',
                    },
                    {
                        value:'彩电',
                    },
                    {
                        value:'二手',
                    },
                    {
                        value:'洗衣液',
                    },
                    {
                        value:'手机',
                    },
                    {
                        value:'冰箱',
                    },
                    {
                        value:'电视',
                    },
                    {
                        value:'茅台',
                    },
                    {
                        value:'笔记本',
                    },
                    {
                        value:'大米',
                    },
                ],
            }
        },
        created(){

        },
        methods:{
            back() {
                this.$router.go(-1);
            },
            goList(query){
                if(!query){
                    this.$toast('请输入关键词');
                    return
                }
                this.$router.push({name: 'GoList', query: {query}});
            },
        }
    }
</script>

<style lang="less" scoped>
</style>